import React from 'react'
import instructor from '../instructors'

const Instructor = () => {
    return (
        
        
        <div className="instructor">
            
            {instructor.map((ins) => (
                <div className="top_ins">
                  <img className="ins_pic" src={ins.image} alt="ins"></img>
                  <h3>{ins.name}</h3>
                  <h4>{ins.company}</h4>
                </div>
            ))
            }
        </div>
        
    )
}

export default Instructor
